import React, { Component } from 'react';
import './App.css';

import { Button } from 'rmwc/Button';

import Navbar from "./Navbar.js";
import Drawer from "./Drawer.js";
import Home from "./Home.js";
import Films from "./Films.js";
import Demarest from "./Demarest.js";
import Tech from "./Tech.js";
import Contact from "./Contact.js";

function returnPage(num) {
  if(num === 0) {
    return <Home />
  } else if (num === 1) {
    return <Films />
  } else if (num === 2) {
    return <Demarest />
  } else if (num === 3) {
    return <Tech />
  } else if (num === 4) {
    return <Contact />
  }
}

export default class App extends Component {
  state = {
    tempOpen: false,
    activeTabIndex: 0
  }

  render() {
    return (
      <div style={{ fontFamily: "Roboto", height: "100%"}}>
        <Navbar 
          menuHandler={() => this.setState({ tempOpen: !this.state.tempOpen })}
          activeTabIndex={this.state.activeTabIndex}
          tabHandler={evt => this.setState({ 'activeTabIndex': evt.target.value })}
        />
        <div className="main-area">
          {returnPage(this.state.activeTabIndex)}
        </div>
        <Drawer
          open={this.state.tempOpen}
          closeHandler={() => this.setState({ tempOpen: false })}
          tabHandler={(e) => this.setState({ 'activeTabIndex': e})}
        />
      </div>
    );
  }
}



