import React from 'react';

import TV from "./TV.js";
import "./Home.css";

export default function Home(props) {
  return <div style={{
    backgroundColor: "black", height: "100%", backgroundImage: "url('./deco-bgd.png')",
    backgroundSize: "cover"}}>
    <TV />
      <div className="home-bio" style={{ width: "80%",
                    color: "white",
                    margin: "0 auto",
                    textAlign: "center",
                    paddingTop: "32px",
                    fontWeight: "100",
                    backgroundColor: "rgba(0, 0, 0, .35)",
                    padding: " 2.5% 2.5% 2.5% 2.5%",
                    marginTop: "32px"
                  }}>
        Our films make a statement, format agnostic, ground-breaking content for the 21st century. Through expertise and endurance we elevate
expectations. The future is crafted by those who dare to dream. Our endeavors embody that ethos. Help us invision a future without limits.
      </div>
    </div>
}


