import React from 'react';



export default function Contact(props) {
  return <div style={{
      backgroundColor: "black", height: "100%", backgroundImage: "url('./deco-bgd.png')",
      backgroundSize: "cover"}}>
      <div style={{
        width: "40%",
        margin: "0 auto",
        display: "flex",
        flexDirection: "column",
        color: "white",
        alignItems: "center",
        textAlign: "center",
        paddingTop: "32px"
      }}>
        <div style={{ paddingTop: "32px" }}>
          <h1>Contact</h1>
          <h2 style={{ paddingTop: "16px" }}>Demarest Media</h2>
          <div style={{ paddingTop: "32px" }}>
            <p>11400 W. Olympic Blvd., Suite 590</p>
            <p>Los Angeles, CA 90064</p>
            <p>424.789.8310</p>
          </div>
          <div style={{ paddingTop: "32px" }}>
            <h2>Jeanne MacLaine</h2>
            <a style={{textDecoration: "none", color: "white"}} href="mailto:jm@demarestfilms.com">jm@demarestfilms.com</a>
            <p>949.396.5070</p>
          </div>
          <p style={{ fontSize: "12px", color: "#999", paddingTop: "64px" }}>
            Demarest Media does not accept unsolicited creative material of any kind, including, but not limited to: ideas, concepts, pitches, suggestions, stories, scripts, treatments, formats, artwork, photographs, reels, musical compositions (including lyrics), and/or other similar works. Any such materials received will be returned or destroyed at the companys discretion.
          </p>
        </div>
      </div>
    </div>
}


