import React, { Component } from 'react';

import "./Navbar.css";

import {
  Toolbar,
  ToolbarRow,
  ToolbarTitle,
  ToolbarFixedAdjust,
  ToolbarMenuIcon,
  ToolbarIcon,
  ToolbarSection
} from 'rmwc/Toolbar';

import { TabBar, Tab, TabIcon, TabIconText, TabBarScroller } from 'rmwc/Tabs';

import { Elevation } from 'rmwc/Elevation';

import logo from './logo.png';

export default function Navbar(props) {
  return <Toolbar style={{ backgroundColor: "black" }}>
      <ToolbarRow>
        <ToolbarSection className="no-desktop" style={{ maxWidth: "64px" }} alignStart>
          <ToolbarMenuIcon use="menu" onClick={props.menuHandler} />
        </ToolbarSection>
        <ToolbarSection style={{ margin: "0 auto" }}>
          <img style={{ height: "64px", "paddingRight": "56px" }} className="logo" alt="logo" src={logo} />
        </ToolbarSection>
      </ToolbarRow>
      <ToolbarRow className="desktop-only">
        <TabBar
          activeTabIndex={props.activeTabIndex}
          onChange={props.tabHandler}
        >
          <Tab>Home</Tab>
          <Tab>Films</Tab>
          <Tab>Demarest</Tab>
          <Tab>Tech</Tab>
          <Tab>Contact</Tab>
        </TabBar>
      </ToolbarRow>
    </Toolbar>
}


