import React, { Component } from 'react';

import {
  Drawer as MDDrawer,
  DrawerHeader,
  DrawerContent
} from 'rmwc/Drawer';

import {
  ListItem,
  ListItemText
} from 'rmwc/List';

import { TabBar, Tab, TabIcon, TabIconText, TabBarScroller } from 'rmwc/Tabs';

import "./Drawer.css"

export default function Drawer(props) {
  return <MDDrawer
    temporary
    open={props.open}
    onClose={props.closeHandler}
  >
    <DrawerHeader style={{ color: "white", backgroundColor: "black" }}>
      Navigation
  </DrawerHeader>
    <DrawerContent style={{ color: "white", backgroundColor: "black" }}>
    {["HOME","FILMS","DEMAREST","TECH","CONTACT"].map((e, i) => {
        return <ListItem className="link-hover" key={i} onClick={() => props.tabHandler(i)}>
          <ListItemText>{e}</ListItemText>
        </ListItem>
    })}
    </DrawerContent>
  </MDDrawer>
}


