import React from 'react';

import "./TV.css"

export default function TV(props) {
  return <div style={{backgroundColor: "black"}}>
    <video loop muted autoPlay poster="img/videoframe.jpg" className="tv">
      <source src="/backVid.mp4" type="video/mp4" />
    </video>
  </div>
}


